import React,{useState} from 'react';
import {connect} from 'react-redux';
import * as actiontypes from './store/actions/request';
const  serachMovieComponent=(props)=>{
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [query,setQuery]=useState('');
    // eslint-disable-next-line react-hooks/rules-of-hooks
 
    const getMovie=async(event)=>{
       event.preventDefault();
     props.onGet(query);
    //  const url= `https://api.themoviedb.org/3/search/movie?api_key=1d547d0f978f704dad5f29a2cd73fe62&language=en-US&query=${query}&inculde_adult=false`;
    //  try{
    //      const res= await fetch(url);
    //      console.log(res);
    //      const data =  await res.json();
    //      console.log(data.results);
    //   setMovie(movie);
 
    //  }catch(err){
    //      console.log(err);
    //  }
      
    }
    
    return (
        <React.Fragment>
<form className="form" onSubmit={getMovie} >
    <label className="label" htmlFor="query">Movie name</label>
<input type="text" className="input" value={query} onChange={(event)=>setQuery(event.target.value)} placeholder="i.e Jurassic Park"/>
<button className="button" type="submit">Search</button>
</form>
<div className="cards_main">
{
    props.res.filter(movie=>movie.poster_path).map(movie=>(
<div className="cards" key={movie.id}>
<img className="image" src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`} alt={`title ${movie.title}`}></img>
    <h3>{movie.title}</h3>
    <p>Release Date: {movie.release_date}</p>
    <p>Positive votes : {movie.vote_count}</p>
    <p className="movie_desc">{movie.overview}</p>
</div>
    ))
}
  
</div>
      </React.Fragment>

    );
}
const mapStateToProps =state=>{
    return{
        res:state.responce
    }
}
const mapDispatchToProps=dispatch=>{
    return{
onGet:(value)=>dispatch(actiontypes.getRequest(value))

    }
}

export default connect(mapStateToProps,mapDispatchToProps)(serachMovieComponent);