import * as actionTypes from '../actions/actionTypes';
import axios from 'axios';
export const request=(value)=>{
    return {
        type:actionTypes.GETQUERY,
        value: value
    }
}


export const getRequest=(value)=>{
    return dispatch=>{
        const url= `https://api.themoviedb.org/3/search/movie?api_key=1d547d0f978f704dad5f29a2cd73fe62&language=en-US&query=${value}&inculde_adult=false`;
        axios.get(url)
        .then(res=>{
       
            dispatch(request(res.data.results))
        }).catch(err=>{
console.log(err);

        });
        // try{
        //     const res= await  fetch(url);
        //     console.log(res);
        //     const data = await res.json();
        //     console.log(data.results);
        //     dispatch(request(data.results));
    
        // }catch(err){
        //     console.log(err);
        // }
    }
}