import * as actionTypes from '../actions/actionTypes';

const initailState={
    responce:[]
}

const reducer=(state=initailState,action)=>{
    switch(action.type){
        case actionTypes.GETQUERY:
            return{
                responce:action.value

            }
            default:
                return{
                  responce:state.responce
                }
    }

}

export default reducer;